___CSS_LOADER_EXPORT___.push([module.id, ".card[data-v-0a17b5cc]{padding:0!important}", "",{"version":3,"sources":["webpack://./resources/js/components/ImageLoader.vue"],"names":[],"mappings":"AA2FA,uBACE,mBACF","sourcesContent":["<template>\n  <LoadingCard\n    ref=\"card\"\n    :loading=\"loading\"\n    class=\"relative border border-lg border-gray-300 dark:border-gray-600 overflow-hidden px-0 py-0\"\n    :class=\"cardClasses\"\n    :style=\"cardStyles\"\n  >\n    <div class=\"missing p-8\" v-if=\"missing\">\n      <p class=\"text-center leading-normal\">\n        <a :href=\"src\" target=\"_blank\">{{ __('This image') }}</a>\n        {{ __('could not be found.') }}\n      </p>\n    </div>\n  </LoadingCard>\n</template>\n\n<script>\nimport { minimum } from '@/util'\n\nexport default {\n  emits: ['missing'],\n\n  props: {\n    src: String,\n\n    maxWidth: {\n      type: Number,\n      default: 320,\n    },\n\n    rounded: {\n      type: Boolean,\n      default: false,\n    },\n  },\n\n  data: () => ({\n    loading: true,\n    missing: false,\n  }),\n\n  computed: {\n    cardClasses() {\n      return {\n        'max-w-xs': !this.maxWidth || this.loading || this.missing,\n        'rounded-full': this.rounded,\n      }\n    },\n\n    cardStyles() {\n      return this.loading\n        ? { height: this.maxWidth + 'px', width: this.maxWidth + 'px' }\n        : null\n    },\n  },\n\n  mounted() {\n    minimum(\n      new Promise((resolve, reject) => {\n        let image = new Image()\n\n        image.addEventListener('load', () => resolve(image))\n        image.addEventListener('error', () => reject())\n\n        image.src = this.src\n      })\n    )\n      .then(image => {\n        image.className = 'block w-full'\n        image.draggable = false\n\n        if (this.maxWidth) {\n          this.$refs.card.$el.style.maxWidth = `${this.maxWidth}px`\n        }\n\n        this.$refs.card.$el.appendChild(image)\n      })\n      .catch(() => {\n        this.missing = true\n\n        this.$emit('missing', true)\n      })\n      .finally(() => {\n        this.loading = false\n      })\n  },\n}\n</script>\n\n<style scoped>\n.card {\n  padding: 0 !important;\n}\n</style>\n"],"sourceRoot":""}]);
